	module.exports = {"data":{"markdownRemark":{"html":"<h2>備考** 2003年にひがし氏が自作PCにチャレンジするということで、自分がパーツ選定したもの。氏がチャレンジするはずが、自分がほぼ全部組み上げてしまった記憶がある。同時期に自作した<a href=\"/Homebuilt-3\">弐號機</a>とスペックが酷似していたので “弐號機Alternative” とこっそり命名。 2010年晩春に<a href=\"http://mixi.jp/view_diary.pl?id=1470950740&#x26;owner_id=824514\">ひがし氏がPC新調する</a>ということで、また自分がパーツ選定して組み上げた際、そのお礼としてタダ同然で譲り受けた。暫くサブPCとして使用していたが、<a href=\"/palx190dr\">dynabookSS LX</a>急逝のため急遽メモリ継ぎ足してメインPCに移行も、<a href=\"/sencia\">ツクモBTO</a>購入により2カ月足らずで再びサブPCに。 Windows7とサウンド機能との相性がひどく(音を鳴らすとカーネル使用率が100%になって、肝心の音もひどい状態になる)、現在はサウンド機能をBIOSレベルで遮断している。ただ、これだと音が鳴らないのでサウンドカード買わなきゃなぁ。少しでもCPU負荷を下げられるようにと<a href=\"http://jp.creative.com/products/product.asp?category=209&#x26;subcategory=669&#x26;product=15853\">SB X-Fi XtremeGamer</a>辺りを検討しているが、どうだろう？ご意見求む。 **製作物・他やってたこと</h2>\n<ul>\n<li>\n<p><a href=\"http://kagaminer.in/\">結界幻想禄 鏡(体験版)</a></p>\n<h2>性能</h2>\n</li>\n<li>\n<p>CPU:intel Pentium4(Northwood/2.8cGHz 1C2T)</p>\n</li>\n<li>\n<p>RAM:4GB(PC-3200)</p>\n</li>\n<li>\n<p>VGA:nVidia GeForceFX5600(VRAM128MB)</p>\n</li>\n<li>\n<p>HDD:80GB(intel SSD)</p>\n</li>\n<li>\n<p>CDD:Logitec 外付けポータブルDVDマルチ</p>\n</li>\n<li>\n<p>FDD:3.5inch×1</p>\n</li>\n<li>\n<p>NET:100Base-T</p>\n</li>\n<li>\n<p>SOUND:AC97</p>\n</li>\n<li>\n<p>OS:Windows7 Enterprise</p>\n<h2>Windows Experience Index</h2>\n</li>\n<li>\n<p>プロセッサ : 3.7</p>\n</li>\n<li>\n<p>メモリ (RAM) : 4.7</p>\n</li>\n<li>\n<p>グラフィックス : <em>2.7</em></p>\n</li>\n<li>\n<p>ゲーム用グラフィックス : 3.0</p>\n</li>\n<li>\n<p>プライマリ ハード ディスク : 6.9</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2010-04-25T16:00:00+09:00","strDate":"2010/4/25","title":"(2010-2012) 弐號機Alternative"}}},"pathContext":{"path":"/ja/blog/2010/04/25-homebuilt-4/","langKey":"ja"}}

